import React, { useState, useEffect } from 'react';
import SearchBar from '../ui/SearchBar/SearchBar';
import UserAccordion from '../ui/Accordion/Accordion';
import jsonData from '../Data/CelebritiesData/CelebritiesData.json'; 
import '../Home/Home.css'


export default function Home() {
  const [searchedUser, setSearchedUser] = useState('');
  const [filteredCelebrities, setFilteredCelebrities] = useState(jsonData);
  const [isEditMode, setIsEditMode] = useState(false);
  const [celebrities, setCelebrities] = useState(jsonData);
  const [noResults, setNoResults] = useState(false);

  const handleEditClick = (id) => {
    setIsEditMode(true);
    console.log(`Edit user with ID: ${id}`);
  };

  const handleDeleteClick = (id) => {
      const updatedCelebrities = celebrities.filter((celebrity) => celebrity.id !== id);
      setCelebrities(updatedCelebrities);
    
  };

  const handleSearch = (value) => {
    setSearchedUser(value);
  };

  useEffect(() => {
    // Filter celebrities based on search term
    const sortedData = celebrities.filter(
      (celebrity) =>
        celebrity.first.toLowerCase().includes(searchedUser.toLowerCase()) ||
        celebrity.last.toLowerCase().includes(searchedUser.toLowerCase()) ||
        celebrity.email.toLowerCase().includes(searchedUser) ||
        celebrity.description.toLowerCase().includes(searchedUser)
    );
    setFilteredCelebrities(sortedData);
    setNoResults(sortedData.length === 0);
  }, [searchedUser, celebrities]); 

  return (
    <div className="home">
      <SearchBar onSearch={handleSearch} />
      <div>
        {noResults ? (
          <p style={{marginLeft:"35%"}}><strong>Invalid Search!!</strong></p>
        ) : (
          filteredCelebrities.map((celebrity) => (
            <UserAccordion
              key={celebrity.id}
              celebrity={celebrity}
              onEdit={handleEditClick}
              onDelete={handleDeleteClick}
            />
          ))
        )}
      </div>
    </div>
  );
          }


