  import React, { useState } from 'react';
  import { 
    Accordion, 
    AccordionSummary, 
    AccordionDetails, 
    Typography, 
    Button, 
    Select, 
    MenuItem, 
    TextareaAutosize,
    Box,
    IconButton,
    Avatar,
    TextField
  } from '@mui/material';
  import ExpandMoreIcon from '@mui/icons-material/ExpandMore';
  import EditIcon from '@mui/icons-material/Edit';
  import DeleteIcon from '@mui/icons-material/Delete';
  import '../Accordion/Accordion.css';


  export default function UserAccordion ({ celebrity, onEdit, onDelete }) {
    const [expanded, setExpanded] = useState(false);
    const [isEditMode, setIsEditMode] = useState(false);
    const [editedUser, setEditedUser] = useState({
      ...celebrity,
    });
    const [description, setDescription] = useState(celebrity.description);

    const toggleAccordion = () => {
      setExpanded(!expanded);
      setIsEditMode(false); 
    };
     
    // Editmode Function
    const handleEditClick = () => {
     
      setIsEditMode(true);
      onEdit(celebrity.id);
    };

    //Save Function
    const handleSaveClick = () => {
      console.log(editedUser)
     
      if (getAge(celebrity.dob) >= 18 && editedUser.first && editedUser.last && editedUser.gender && editedUser.country && description) {
       
        setIsEditMode(false);
      }
      else {
        alert('Please check your age or any field is empty!!');
      }
    };

    const handleCancelClick = () => {

      setEditedUser({ ...celebrity });
      setDescription(celebrity.description);
      setIsEditMode(false);
    };


    
    const handleDeleteClick = () => {
      // console.log('Delete button clicked for ID:', celebrity.id);
   
      const confirmDelete = window.confirm('Are you sure you want to delete this user?');
       if (confirmDelete) {
        onDelete(celebrity.id);
      }
    };
  

    // Calculating Age from Dob

    const getAge = (dateString) => {
      var today = new Date();
      var birthDate = new Date(dateString);
      var age = today.getFullYear() - birthDate.getFullYear();
      var m = today.getMonth() - birthDate.getMonth();
      if (m < 0 || (m === 0 && today.getDate() < birthDate.getDate())) {
        age--;
      }
      return age;
    };

    return (
      <div>

      <Accordion expanded={expanded} onChange={toggleAccordion} className="accordion" style={{borderRadius:'30px', marginBottom: '16px', width: '30%', marginLeft: '30%' }}>
        <AccordionSummary 
        expandIcon={<ExpandMoreIcon />}
        >
          <Avatar alt="User-Img" src={celebrity.picture} style={{ marginRight: '10px', width: '50px', height: '50px' }} />
          <Typography variant="h5">{celebrity.first} {celebrity.last}</Typography>
        </AccordionSummary>
        <AccordionDetails className="accordion-details">
          {expanded && (
            <div className="details-row">
              <div className="label-value-pair">
                <Typography className="detail-label">Age:</Typography>
                <Typography className="detail-value">{getAge(celebrity.dob)}</Typography>
              </div>
              <div className="label-value-pair" style={{ marginLeft: '60px' }}>
                <Typography className="detail-label">Gender:</Typography>
                {isEditMode ? (
                  <Select
                  label="Gender"
                  value={editedUser.gender}
                  onChange={(e) => setEditedUser({ ...editedUser, gender: e.target.value })}
                  style={{ borderRadius: '10px' , height:"30px", width:'100px'}}
                  >
                    
                    <MenuItem value="Male">Male</MenuItem>
                    <MenuItem value="Female">Female</MenuItem>
                    <MenuItem value="Transgender">Transgender</MenuItem>
                    <MenuItem value="Rather Not To Say">Rather Not To Say</MenuItem>

                  </Select>
                ) : (
                  <Typography className="detail-value">{editedUser.gender}</Typography>
                  )}
              </div>
              <div className="label-value-pair" style={{ marginLeft: '60px', borderRadius: '90px'}}>
                <Typography className="detail-label">Country:</Typography>
                <Typography className="detail-value">{editedUser.country}</Typography>
                
              </div>
            </div>
          )}
          <Typography className="detail-label">Description:</Typography>
          {isEditMode ? (
            <TextareaAutosize 
            minRows={3}
            placeholder="Enter description"
            value={description}
            onChange={(e) => setDescription(e.target.value)}
            className="description-textarea"
            />
            ) : (
              <Typography className="description-text">{description}</Typography>
              )}
          {expanded && (
            <div className="actions-row" style={{ marginTop: '20px' }}>
              {isEditMode ? (
                <>
                  <Button onClick={handleSaveClick} variant="contained" color="primary">
                    Save
                  </Button>
                  <Button onClick={handleCancelClick} variant="outlined" color="secondary" style={{ marginLeft: '10px' }}>
                    Cancel
                  </Button>
                </>
              ) : (
                <>
                  <IconButton onClick={handleEditClick} color="primary">
                    <EditIcon />
                  </IconButton>
                  <IconButton onClick={handleDeleteClick} color="secondary">
                    <DeleteIcon />
                  </IconButton>
                </>
              )}
            </div>
          )}
        </AccordionDetails>
      </Accordion>

      {/* Delete dialog box */}
      
      
      </div>
       
    );
  };

 
